export default {
	props:{
		pageSize:{
			type:Number, 
			default:10
		},
		color: {
			type: String,
			default: '#777777'
		}
	},
	computed:{
		StateText(){
			var text = '';
			switch(this.state){
				case 0:text = '上拉加载更多...';break;
				case 1:text = '加载中，请稍后...';break;
				case 2:text = '无更多内容...';break;
				case 3:text = '发生错误，请重试...';break;
				default:text = '无更多内容...';break;
			}
			return text;
		},
		items(){
			return this.dataList.concat(this.lastPage)
		}
	},
	data() {
		return {
			state:0,
			dataList:[],
			lastPage:[],
			pageNo:1,
			
		}
	},
	methods: {
		pushData(arr){
			
			if(arr && arr.length>=0){
				this.state = 0;
			}else{
				//TODO handle the exception
				this.state = 3;
				return;
			}
			
			if(arr.length < this.pageSize)
			{
				this.state = 2;
				this.lastPage = arr;
			}
			else{
				this.pageNo ++;
				this.dataList = this.dataList.concat(arr);
				this.lastPage = [];
			}
			
		},
		provideData(){
			this.$emit('provider',{
				pageNo:this.pageNo,
				pageSize:this.pageSize
			})
		},
		reLoadData(){
			if(this.state === 1)
			{
				return ;
			}
			this.state = 1;
			
			this.dataList = [],
			this.pageNo = 1,
			this.provideData();
		},
		reachBottom(){
			if(this.state === 1)
			{
				return ;
			}
			this.state = 1;
			
			this.provideData()
		},
		pullDownRefresh(){
			this.reLoadData()
			uni.stopPullDownRefresh()
		}
	}
}
